import { useEffect, useState } from 'react'
import { Session } from '@supabase/supabase-js'
import { supabase } from '@/lib/supabase'
import Navbar from '@/components/Navbar'
import Hero from '@/components/Hero'
import Cart from '@/components/Cart'
import MiniCart from '@/components/MiniCart'
import ShopStatusBanner from '@/components/ShopStatusBanner'
import { ShoppingCart, Star } from 'lucide-react'
import Link from 'next/link'
import IceCreamPortionSelector from '@/components/IceCreamPortionSelector'

interface Product {
  id: string
  name: string
  description: string
  price: number
  category: string
  icon: string
  available: boolean
  scoop_count?: number
  image_url?: string
}

interface CartItem extends Product {
  quantity: number
}

export default function Home({ session }: { session: Session | null }) {
  const [products, setProducts] = useState<Product[]>([])
  const [cart, setCart] = useState<CartItem[]>([])
  const [selectedCategory, setSelectedCategory] = useState('all')
  const [isCartOpen, setIsCartOpen] = useState(false)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    fetchProducts()
    loadCartFromStorage()
  }, [])

  useEffect(() => {
    saveCartToStorage()
  }, [cart])

  const fetchProducts = async () => {
    try {
      const { data, error } = await supabase
        .from('products')
        .select('*')
        .eq('available', true)
        .order('category', { ascending: true })

      if (error) throw error
      setProducts(data || [])
    } catch (error) {
      console.error('Error fetching products:', error)
    } finally {
      setLoading(false)
    }
  }

  const loadCartFromStorage = () => {
    // Vereinheitlichter Key passend zum Checkout
    const saved = localStorage.getItem('simonetti-cart')
    if (saved) setCart(JSON.parse(saved))
  }

  const saveCartToStorage = () => {
    localStorage.setItem('simonetti-cart', JSON.stringify(cart))
  }

  const addToCart = (product: any) => {
    setCart(prevCart => {
      if (product.baseProduct) {
        return [...prevCart, { ...product, quantity: 1 }]
      }
      const existing = prevCart.find(item => item.id === product.id)
      if (existing) {
        return prevCart.map(item =>
          item.id === product.id ? { ...item, quantity: item.quantity + 1 } : item
        )
      }
      return [...prevCart, { ...product, quantity: 1 }]
    })
  }

  const updateQuantity = (productId: string, change: number) => {
    setCart(prevCart =>
      prevCart
        .map(item => item.id === productId ? { ...item, quantity: item.quantity + change } : item)
        .filter(item => item.quantity > 0)
    )
  }

  const removeFromCart = (productId: string) => {
    setCart(prevCart => prevCart.filter(item => item.id !== productId))
  }

  const clearCart = () => setCart([])

  const cartTotal = cart.reduce((sum, item) => sum + item.price * item.quantity, 0)
  const cartCount = cart.reduce((sum, item) => sum + item.quantity, 0)

  const categories = ['all', ...Array.from(new Set(products.map(p => p.category)))]
  const filteredProducts = selectedCategory === 'all'
    ? products
    : products.filter(p => p.category === selectedCategory)

  return (
    <div style={{ backgroundColor: '#fdfcfb' }} className="min-h-screen">
      <Navbar session={session} cartCount={cartCount} onCartClick={() => setIsCartOpen(true)} />
      
      {/* Shop Status Banner - Direkt unter Navbar */}
      <ShopStatusBanner />

      <main>
        <Hero />

        <div id="produkte" className="max-w-7xl mx-auto px-6 lg:px-8 py-16">

          {/* Kategorien */}
          <div className="flex gap-3 mb-12 overflow-x-auto pb-4 no-scrollbar">
            {categories.map(category => (
              <button
                key={category}
                onClick={() => setSelectedCategory(category)}
                className="px-8 py-3.5 rounded-full font-bold whitespace-nowrap transition-all duration-300 text-sm tracking-wide uppercase"
                style={selectedCategory === category
                  ? { backgroundColor: '#4a5d54', color: '#fdfcfb', boxShadow: '0 8px 20px rgba(74,93,84,0.2)' }
                  : { backgroundColor: 'white', color: '#4a5d54', border: '1px solid #eee' }
                }
              >
                {category === 'all' ? 'üç¶ Alle' : `${getCategoryIcon(category)} ${category}`}
              </button>
            ))}
          </div>

          {/* Produkte Grid */}
          {loading ? (
            <div className="flex justify-center items-center py-32">
              <div className="text-center">
                <div className="text-6xl mb-6 animate-bounce">üç¶</div>
                <div className="font-display italic text-2xl" style={{ color: '#4a5d54' }}>Unsere Eissorten werden vorbereitet...</div>
              </div>
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-10">
              {filteredProducts.map(product => (
                <div key={product.id} className="bg-white rounded-[2.5rem] p-8 shadow-sm border border-gray-50 flex flex-col transition-all duration-500 hover:shadow-xl hover:-translate-y-1 group">
                  
                  <div className="mb-6 relative overflow-hidden rounded-[2rem] h-56 bg-[#f9f8f4]">
                    {product.image_url ? (
                      <img 
                        src={product.image_url} 
                        alt={product.name}
                        className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110"
                      />
                    ) : (
                      <div className="w-full h-full flex items-center justify-center text-7xl bg-[#f9f8f4]">
                        {product.icon}
                      </div>
                    )}
                    <div className="absolute top-4 right-4 bg-white/90 backdrop-blur-sm px-3 py-1 rounded-full text-[10px] font-black uppercase tracking-widest text-[#4a5d54]">
                      {product.category}
                    </div>
                  </div>

                  <h3 className="text-2xl font-display font-bold italic mb-3 text-center" style={{ color: '#4a5d54' }}>{product.name}</h3>
                  <p className="text-gray-400 text-sm mb-6 text-center flex-grow leading-relaxed font-medium">{product.description}</p>
                  
                  {product.category === 'Eisportionen' && product.scoop_count && product.scoop_count > 0 ? (
                    <IceCreamPortionSelector 
                      product={product}
                      availableFlavors={products.filter(p => 
                        (p.category === 'Eis' || p.category === 'Sorbet') && p.available
                      )}
                      onAddToCart={addToCart}
                    />
                  ) : (
                    <div className="text-center pt-4 border-t border-gray-50">
                      <div className="text-3xl font-bold mb-5" style={{ color: '#4a5d54' }}>
                        {product.price.toFixed(2)} <span className="text-sm font-medium">‚Ç¨</span>
                      </div>
                      <button 
                        onClick={() => addToCart(product)} 
                        className="w-full py-4 rounded-2xl font-bold text-white transition-all duration-300 shadow-lg hover:shadow-[#4a5d54]/20 active:scale-95"
                        style={{ backgroundColor: '#4a5d54' }}
                      >
                        In den Warenkorb
                      </button>
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>

        <MiniCart 
          cart={cart} 
          total={cartTotal} 
          onOpenCart={() => setIsCartOpen(true)} 
        />
      </main>

      <footer style={{ backgroundColor: '#4a5d54', color: '#fdfcfb' }} className="mt-20 py-20 px-6">
        <div className="max-w-7xl mx-auto">
          <div className="flex flex-col md:flex-row justify-between items-start mb-16 gap-12">
            <div>
              <h2 className="text-4xl font-display font-bold italic mb-6">Simonetti</h2>
              <p className="text-[#8da399] max-w-sm leading-loose font-medium">
                Traditionelles Gelato-Handwerk in Langenfeld. Wir verwenden nur die besten Zutaten f√ºr den echten italienischen Genuss.
              </p>
            </div>
            <div className="grid grid-cols-2 gap-20">
              <div className="flex flex-col gap-4">
                <span className="text-[10px] font-black tracking-[0.2em] text-[#8da399] uppercase">Entdecken</span>
                <Link href="/" className="text-white/80 no-underline hover:text-white transition-colors font-semibold">Eiskarte</Link>
                <Link href="/account" className="text-white/80 no-underline hover:text-white transition-colors font-semibold">Mein Konto</Link>
              </div>
              <div className="flex flex-col gap-4">
                <span className="text-[10px] font-black tracking-[0.2em] text-[#8da399] uppercase">Rechtliches</span>
                <Link href="/impressum" className="text-white/80 no-underline hover:text-white transition-colors font-semibold">Impressum</Link>
                <Link href="/datenschutz" className="text-white/80 no-underline hover:text-white transition-colors font-semibold">Datenschutz</Link>
                <Link href="/agb" className="text-white/80 no-underline hover:text-white transition-colors font-semibold">AGB</Link>
              </div>
            </div>
          </div>
          <div style={{ borderTop: '1px solid rgba(253,252,251,0.1)' }} className="pt-8 flex flex-col md:flex-row justify-between items-center text-xs font-bold tracking-widest text-[#8da399] uppercase">
            <span>¬© {new Date().getFullYear()} Simonetti Gelateria Langenfeld</span>
            <span className="mt-4 md:mt-0">üç¶ Handgemacht mit Liebe</span>
          </div>
        </div>
      </footer>

      <Cart
        isOpen={isCartOpen}
        onClose={() => setIsCartOpen(false)}
        cart={cart}
        onUpdateQuantity={updateQuantity}
        onRemoveItem={removeFromCart}
        onClearCart={clearCart}
        total={cartTotal}
        session={session}
      />
    </div>
  )
}

function getCategoryIcon(category: string): string {
  const icons: Record<string, string> = {
    'Eis': 'üç¶',
    'Sorbet': 'üçß',
    'Eisportionen': 'üç®',
    'Spezialit√§ten': '‚≠ê',
    'Getr√§nke': 'ü•§',
    'Waffeln': 'üßá',
  }
  return icons[category] || 'üç¶'
}